import { useState, useEffect, useCallback } from "react";
import {
  getAllCategories,
  getAllFeaturedCategories,
  getCategory,
} from "../api/categoryApis";

const useCategory = (categoryId) => {
  const [categories, setCategories] = useState([]);
  const [featuredCategories, setFeaturedCategories] = useState([]);
  const [category, setCategory] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAllCategories = useCallback(async () => {
    if (categoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "categories";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setCategories(JSON.parse(cachedData));
      } else {
        const response = await getAllCategories();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.mainCategoryList)
          );
          setCategories(response.data.mainCategoryList);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [categoryId]);

  const fetchAllFeaturedCategories = useCallback(async () => {
    if (categoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "featured_categories";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setFeaturedCategories(JSON.parse(cachedData));
      } else {
        const response = await getAllFeaturedCategories();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.categoryDetails)
          );
          setFeaturedCategories(response.data.categoryDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [categoryId]);

  const fetchCategory = useCallback(async () => {
    if (!categoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `category_${categoryId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setCategory(JSON.parse(cachedData));
      } else {
        const response = await getCategory(categoryId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.categoryDetails)
          );
          setCategory(response.data.categoryDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [categoryId]);

  useEffect(() => {
    fetchAllCategories();
  }, [fetchAllCategories]);

  useEffect(() => {
    fetchAllFeaturedCategories();
  }, [fetchAllFeaturedCategories]);

  useEffect(() => {
    fetchCategory();
  }, [fetchCategory]);

  return {
    categories,
    featuredCategories,
    category,
    loading,
    error,
    refresh: fetchAllCategories,
  };
};

export default useCategory;
