import { useState, useEffect, useCallback } from "react";
import {
  getAllFeaturedSubCategories,
  getAllSubcategories,
  getSubcategories,
  getSubcategory,
} from "../api/categoryApis";

const useSubcategory = (categoryId, subcategoryId) => {
  const [allSubcategories, setAllSubcategories] = useState([]);
  const [allFeaturedSubcategories, setAllFeaturedSubcategories] = useState([]);
  const [subcategories, setSubcategories] = useState([]);
  const [subcategory, setSubcategory] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAllSubcategories = useCallback(async () => {
    if (categoryId || subcategoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "subcategories";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setAllSubcategories(JSON.parse(cachedData));
      } else {
        const response = await getAllSubcategories();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.subCategoryList)
          );
          setAllSubcategories(response.data.subCategoryList);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [categoryId, subcategoryId]);

  const fetchAllFeaturedSubcategories = useCallback(async () => {
    if (categoryId || subcategoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "featured_subcategories";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setAllFeaturedSubcategories(JSON.parse(cachedData));
      } else {
        const response = await getAllFeaturedSubCategories();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.subCategoryDetails)
          );
          setAllFeaturedSubcategories(response.data.subCategoryDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [categoryId, subcategoryId]);

  const fetchSubcategoriesByCategory = useCallback(async () => {
    if (!categoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `subcategories_${categoryId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setSubcategories(JSON.parse(cachedData));
      } else {
        const response = await getSubcategories(categoryId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.subCategoryList)
          );
          setSubcategories(response.data.subCategoryList);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [categoryId]);

  const fetchSubcategory = useCallback(async () => {
    if (!subcategoryId) return;
    setLoading(true);
    setError(null);
    try {
      const cacheKey = `subcategory_${subcategoryId}`;
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setSubcategory(JSON.parse(cachedData));
      } else {
        const response = await getSubcategory(subcategoryId);

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.subCategoryIdDetails)
          );
          setSubcategory(response.data.subCategoryIdDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, [subcategoryId]);

  useEffect(() => {
    fetchAllSubcategories();
  }, [fetchAllSubcategories]);

  useEffect(() => {
    fetchAllFeaturedSubcategories();
  }, [fetchAllFeaturedSubcategories]);

  useEffect(() => {
    fetchSubcategoriesByCategory();
  }, [fetchSubcategoriesByCategory]);

  useEffect(() => {
    fetchSubcategory();
  }, [fetchSubcategory]);

  return {
    allSubcategories,
    allFeaturedSubcategories,
    subcategories,
    subcategory,
    loading,
    error,
    refresh: fetchAllSubcategories,
  };
};

export default useSubcategory;
