import useMediaQuery from "../hooks/useMediaQuery";
import { useBanners } from "../services/bannerServices";
import Carousel from "./Carousel";
import Skeleton from "./Skeleton";

const SquareBanner = ({
  position,
  resourceType,
  valueId,
  title,
  subtitle,
  className,
  containerClassName = "container max-w-full",
}) => {
  const { data: banners = [], isLoading } = useBanners({
    bannerType: "Square",
    position,
    resourceType,
    valueId,
  });
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  if (isLoading) {
    return (
      <ul className={`flex gap-4 overflow-x-hidden ${containerClassName}`}>
        {Array.from({ length: 10 }).map((_, index) => (
          <li
            key={index}
            className={`size-[500px] max-xl:size-[400px] max-lg:size-[300px] rounded-lg overflow-hidden flex-shrink-0 ${className}`}
          >
            <Skeleton />
          </li>
        ))}
      </ul>
    );
  }

  return (
    banners?.length > 0 && (
      <div className={`space-y-4 max-lg:space-y-2 ${containerClassName}`}>
        {title || subtitle ? (
          <div>
            <h1 className="text-2xl max-lg:text-xl font-bold">{title}</h1>
            <p className="text-sm">{subtitle}</p>
          </div>
        ) : null}

        <Carousel
          slideWidth={maxLg ? 300 : maxXl ? 400 : 500}
          navigation
          autoplay
          delay={5000}
          spaceBetween={maxLg ? 10 : 14}
          allowTouchMove
        >
          {banners.map((banner) => (
            <a
              key={banner.bannerTitle || banner._id}
              href={banner.linkUrl}
              className="block relative"
            >
              <img
                src={banner.bannerImg}
                alt={banner.bannerTitle || banner._id}
                loading="lazy"
                className={`size-[500px] max-xl:size-[400px] max-lg:size-[300px] object-cover object-center rounded-lg ${className}`}
              />
            </a>
          ))}
        </Carousel>
      </div>
    )
  );
};

export default SquareBanner;
